<template>
  <div class="hot" :class="{ 'active-botton': playbarshow }">
    <div class="hot-img">
      <div>
        <div class="hot-title">
          <!-- <i>云音乐</i> -->
          <div>
            <p>更新时间: {{ updateTime }}</p>
          </div>
        </div>
      </div>
    </div>
    <ul class="hot-ul">
      <SongList
        v-for="(item, index) in tracks"
        :key="item.id"
        :item="item"
        @song-id="
          $emit('song-id', $event), $emit('song-play-list', hotSongs.tracks)
        "
        :playing="playing"
        :currentId="currentId"
        class="hot-list"
        ><span>{{ (index + 1).toString().padStart(2, "0") }}</span></SongList
      >
    </ul>
  </div>
</template>

<script>
// import HotSongList from "../components/HotSongList.vue";
import SongList from "../components/SongList.vue";

export default {
  props: {
    playing: Boolean,
    currentId: Number,
    playbarshow: Boolean,
  },
  data() {
    return {
      hotSongs: "",
      // SongList: "",
      tracks: "",
    };
  },
  components: {
    // HotSongList,
    SongList,
  },
  created() {
    this.axios
      .get("http://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        // console.log(res.data.playlist.tracks);
        this.hotSongs = res.data.playlist;
        this.tracks = this.hotSongs.tracks.slice(0, 50);
        // console.log(this.tracks);
      });
  },
  computed: {
    updateTime() {
      var date = new Date();
      var month = date.getMonth() + 1;
      var day = date
        .getDate()
        .toString()
        .padStart(2, "0");
      return month.toString().padStart(2, "0") + "月" + day + "日";
    },
  },
};
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}
.hot {
  position: absolute;
  top: 140px;
  left: 0px;
  width: 100%;
  height: 80%;
  overflow-y: auto;
  margin-bottom: 20px;
  .hot-img {
    position: relative;
    width: 100%;
    height: 146px;
    background-image: url("./../assets/hot_music_bg_3x.jpg");
    background-size: contain;
    background-image: no-repeat;
    z-index: -1;
    &::after {
      position: absolute;
      top: 0;
      left: 0;
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.329);
      z-index: -1;
    }
    .hot-title {
      padding-top: 25px;
      // h1 {
      //   font-size: 50px;
      // }
      div {
        background: url("./../assets/index_icon_2x.png") no-repeat -10px -26px;
        width: 283px;
        height: 133px;
        background-size: 60%;
        p {
          position: absolute;
          bottom: 20px;
          left: 12px;
          color: white;
        }
      }
    }
  }
  .hot-ul {
    .hot-list {
      margin: 0;
      padding: 10px;
      .num {
        width: 30px;
        color: #999;
        text-align: center;
        font-size: 17px;
      }
    }
  }
}
.active-botton {
  // padding-bottom: 100px;
  // height: 100%;
  height: 69vh;
}
</style>
